@import "../../styles/helpers";

.login {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 48px;
    background: $n1;
    @include m {
        padding: 24px; }
    @include dark {
        background: $n7; } }

.wrapper {
    max-width: 296px;
    @include m {
        max-width: 100%; } }

.logo {
    display: inline-block;
    width: 100px;
    margin-bottom: 32px;
    img {
        width: 100%; } }

.title {
    margin-bottom: 32px; }

.head {
    margin-bottom: 32px;
    padding-bottom: 32px;
    border-bottom: 2px solid $n3;
    @include dark {
        border-color: $n6; } }

.subtitle {
    margin-bottom: 20px;
    @include body2-s; }

.btns {
    display: flex;
    margin: 0 -4px;
    .button {
        flex: 0 0 calc(50% - 8px);
        width: calc(50% - 8px);
        margin: 0 4px;
        padding: 0 16px;
        img {
            margin-right: 10px; } } }

.body {
    .button {
        width: 100%; } }

.field {
    margin-bottom: 12px; }

.note {
    margin-top: 32px;
    @include body2-s;
    color: $shades1; }

.info {
    margin-top: 32px;
    @include caption1;
    color: $n4;
    a {
        font-weight: 700;
        color: $n7;
        transition: color .2s;
        @include dark {
            color: $n1; }
        &:hover {
            color: $p1; } } }


.emailWrapper {
    position: relative; }

.hasError {
    margin-bottom: 12px;
    input {
        background: #FFBC9925 !important;
        color: $p3;
        padding-right: 36px;
        @include base1-s;
        &:focus {
            border-color: #FFBC9925; }
        @include dark {
            color: $p3;
            &:focus {
                border-color: $p3; } } } }

.field {
    margin-bottom: 12px;
    input {
        padding-right: 36px; } }


.warningIcon {
    position: absolute;
    fill: $p3;
    right: 8px;
    top: 0;
    background: transparent;
    height: 48px; }

.validIcon {
    position: absolute;
    fill: $p2;
    right: 8px;
    top: 0;
    background: transparent;
    height: 48px; }
