[class^="status"] {
    display: inline-block;
    padding: 0 8px;
    // background: rgba($dark3, .1)
    border-radius: 6px;
    font-size: 12px;
    line-height: 24px;
    font-weight: 700;
 }    // color: $dark2

.status-green {
    background: #EAFAE5;
    color: $p2;
    @include dark-common {
        background: rgba(#83BF6E, .15); } }

.status-green-dark {
    background: $s4;
    color: $n7; }

.status-red {
    background: #FFE7E4;
    color: $p3;
    @include dark-common {
        background: rgba($p3, .15); } }

.status-red-dark {
    background: $s1;
    color: $n1; }

.status-purple {
    background: #CABDFF;
    color: $p4; }

.status-blue {
    background: $s3;
    color: $p1; }

.status-yellow {
    background: $s5;
    color: $n7; }
