@import "../../styles/helpers";

.page {
    padding: 96px 0 0 340px;
    @include x {
        padding: 80px 0 0 300px; }
    @include d {
        padding-left: 96px; }
    @include t {
        overflow: hidden; }
    @include m {
        padding-left: 0; } }

.sidebar {
    @include m {
        transform: translateX(-100%);
        transition: transform .3s;
        &.visible {
            transform: translateX(0); } } }

.inner {
    display: flex;
    flex-direction: column;
    min-height: calc(100vh - 96px);
    padding: 40px;
    @include x {
        padding: 32px 24px; }
    @include m {
        padding: 24px 16px; } }

.container {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    &.wide {
        max-width: 100%; } }

.title {
    margin-bottom: 24px;
    @include m {
        margin-bottom: 16px; } }
