@import "../../../styles/helpers";

.info {
    margin-bottom: 20px;
    @include body2-s; }

.fieldset {
    margin: 0 -6px; }

.field {
    input {
        flex: 0 0 calc(25% - 12px);
        width: calc(25% - 12px) !important;
        margin: 0 6px !important;
        height: 72px !important;
        padding: 0 6px;
        border-radius: 12px;
        border: 2px solid transparent;
        background: $n3;
        text-align: center;
        @include inter;
        font-size: 48px;
        font-weight: 600;
        color: $n7;
        transition: all .2s;
        &:focus {
            border-color: $shades1;
            background: $n; }
        @include dark {
            background: $n6;
            color: $n1;
            &:focus {
                background: transparent; } }
        &.error {
            .input {
                background: rgba($s1, .25);
                border-color: transparent;
                color: $p3; } } } }

.resendCode {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    margin: 16px 6px 0;
    span {
        margin-right: 6px; } }

.errorNote {
    display: none;
    margin-top: 12px;
    @include caption2;
    font-weight: 500;
    color: $p3; }

.button {
    width: 100%;
    margin-top: 12px;
    .loader {
        display: none;
        margin-right: 16px; } }

.note {
    margin-top: 32px;
    @include body2-s;
    color: $shades1; }
