@import "../../../../../styles/helpers";
.card {
    background-color: $s2;
    padding: 16px;
    border-radius: 8px;
    max-width: 300px;
    .cardHead {
        display: flex;
        justify-content: space-between;
        .pic {
            max-width: 35px;
            height: 10px; }
        .headRight {
            svg {
                fill: $n4;
                margin-left: 8px; } } }

    .cardNumber {
        margin-top: 6px;
        span {
            @include caption2-m;
            color: $n4;
            @include x {
                font-size: 10px; }
            @include t {
                font-size: 12px; }
            @include s {
                font-size: 10px; } } }

    .cardName {
        @include caption2-m;
        color: $n4;
        margin-top: 6px; }

    .cardFooter {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        margin-top: 6px;
        .startDate,
        .endDate {
            @include title1-m;
            color: $n4; }
        .cardLogo {
            height: 16px;
            width: 31px; } } }
