@import "../../styles/helpers";

.cardHead {
    display: flex;
    justify-content: space-between;
    .pic {
        max-width: 70px;
        height: 20px; }
    .headRight {
        svg {
            fill: $n4;
            margin-left: 8px; } } }

.cardNumber {
    margin-top: 16px;
    span {
        color: $n4;
        font-size: 24px;
        font-weight: 600; } }

.cardName {
    @include base2;
    color: $n4; }

.cardFooter {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 20px;
    .startDate,
    .endDate {
        @include title1-m;
        color: $n4; }
    .cardLogo {
        height: 37px;
        width: 73px; } }
