$p2: #FFDD0F;
$p3: #FF6A55;
$p4: #8E59FF;
$p5: #FFDD0F;
$p6: #00AE4F;
$s1: #FFBC99;
$s2: #CABDFF;
$s3: #B1E5FC;
$s4: #00AE4F;
$s5: #FFD88D;

$n: #FFFFFF;
$n1: #FCFCFC;
$n2: #F4F4F4;
